import * as PIXI from 'pixi.js';
import { setupEvents } from './event';
import {SingleGame} from './state';

//Aliases
const Application = PIXI.Application,
  Container = PIXI.Container,
  resources = PIXI.Loader.shared.resources,
  Texture = PIXI.Texture,
  Sprite = PIXI.Sprite,
  AnimatedSprite = PIXI.AnimatedSprite,
  Rectangle = PIXI.Rectangle;


const animated = "mg"

const boardInString = [
  '          ',
  '          ',
  '     ',
  '  m   g  ',
  '          ',
  '          ',
].map((s) => s.split(''));

const game = new SingleGame(boardInString);

//Create a Pixi Application
const app = new PIXI.Application({
  width: 800,
  height: 450,
  antialias: false,
  resolution: devicePixelRatio,
  backgroundColor: 0x262b44
});
const container = document.querySelector('.container');
if (container !== null) {
  container.appendChild(app.view);
}

const loader = PIXI.Loader.shared;
//load an image and run the `setup` function when it's done
loader.add('sprite', 'spritesheet.json').load(setup);

let gameArea = new PIXI.Container();
app.stage.addChild(gameArea);

let screen: PIXI.AnimatedSprite[][] = [];

function areaCenter() {
  const bbox = gameArea.getBounds(); 
  gameArea.x = (app.screen.width - bbox.width) * 0.5;
  gameArea.y = (app.screen.height - bbox.height) * 0.5;

  const graphics = new PIXI.Graphics();
  graphics.beginFill(0x000000);
  graphics.drawRect(gameArea.x, gameArea.y, gameArea.width, gameArea.height); 
  app.stage.addChildAt(graphics, 0);
}

function setup() {
  //Create the `tileset` sprite from the texture
  for (let i = 0; i < game.board.N; i++) {
    let newSpriteRow: PIXI.AnimatedSprite[] = [];
    for (let j = 0; j < game.board.M; j++) {
      const element =
        game.board.elements[i][j].value === ' ' ? 'Z' : game.board.elements[i][j].value;
      
      let textures;
      if (animated.includes(element)) {
        textures = [PIXI.Texture.from(`${element}0.gif`), PIXI.Texture.from(`${element}1.gif`), PIXI.Texture.from(`${element}2.gif`)]
      } else {
        textures = [PIXI.Texture.from(`${element}.gif`)];
      }
      const elementSprite = new PIXI.AnimatedSprite(textures);
      elementSprite.x = j * 48;
      elementSprite.y = i * 48;
      elementSprite.animationSpeed = 0.05;
      if (!game.board.elements[i][j].activated) {
        elementSprite.alpha = 0.5;
      }
      elementSprite.play();
      newSpriteRow.push(elementSprite);
      gameArea.addChild(elementSprite);
    }
    screen.push(newSpriteRow);
  }
  areaCenter();
  game.renderer = update;
  setupEvents(window, game);

  //Start the game loop
  // app.ticker.add(delta => gameLoop(delta));
  app.start();
}

function loadMultipleTextures(name: string, size: number) {
  const textures: PIXI.Texture[] = [];
  for (let i=0; i<size; i++) {
    const texture = PIXI.Texture.from(`${name}${i}.gif`);
    if (texture !== undefined) {
      textures.push(texture);
    }
  } 
  return textures;
}

function glitterEffect() {
  const sprite = new PIXI.AnimatedSprite(loadMultipleTextures('glitter', 12));
  sprite.animationSpeed = 0.1;
  return sprite;
}

let removeEffect: PIXI.AnimatedSprite[] = [];

function win() {
  const sprite = new PIXI.AnimatedSprite(loadMultipleTextures('clear', 4));
  sprite.width /= 1.5;
  sprite.height /= 1.5;
  sprite.animationSpeed = 0.1; 
  sprite.loop = false;
  sprite.x = (app.screen.width - sprite.width) * 0.5;
  sprite.y = (app.screen.height - sprite.height) * 0.5;
  app.stage.addChild(sprite);
  gameArea.alpha = 0.5;
  sprite.play();
}

function update() {
  for (let effect of removeEffect){
    gameArea.removeChild(effect);
  }
  removeEffect = [];
  if (game.flags.won) {
    win();
  }
  for (let i = 0; i < game.board.N; i++) {
    for (let j = 0; j < game.board.M; j++) {
      const element =
        game.board.elements[i][j].value === ' ' ? 'Z' : game.board.elements[i][j].value;
      let textures;
      if (animated.includes(element)) {
        textures = [PIXI.Texture.from(`${element}0.gif`), PIXI.Texture.from(`${element}1.gif`), PIXI.Texture.from(`${element}2.gif`)]
      } else {
        textures = [PIXI.Texture.from(`${element}.gif`)];
      }
      if (!game.board.elements[i][j].activated) {
        screen[i][j].alpha = 0.5;
      } else {
        screen[i][j].alpha = 1;
      }
      if (game.keepFlags.wedding && element === 'g') {
        const sprite = glitterEffect();
        sprite.x = screen[i][j].x;
        sprite.y = screen[i][j].y;
        sprite.play();
        gameArea.addChild(sprite);
        removeEffect.push(sprite); 
      }
      screen[i][j].textures = textures;
      screen[i][j].gotoAndPlay(screen[i][j].currentFrame);
    }
  }
}