import { time } from "console";
import { Direction, SingleGame } from "./state";

export function setupEvents(window: Window, game: SingleGame) {
    let keyEvents = new Map<string, ()=>void>();
    keyEvents.set('ArrowUp', ()=>{ game.move('UP'); })
    keyEvents.set('ArrowDown', ()=>{ game.move('DOWN'); })
    keyEvents.set('ArrowLeft', ()=>{ game.move('LEFT'); })
    keyEvents.set('ArrowRight', ()=>{ game.move('RIGHT'); })

    window.addEventListener('keydown', (event:KeyboardEvent) => {
        let func = keyEvents.get(event.code);
        if (func !== undefined) {
            func();
        }
    }, false);

    let timelyMove: {
        dir: Direction,
        timer: ReturnType<typeof setTimeout>
    } | undefined;
    let touchX: number, touchY: number;
    window.addEventListener('touchstart', (event:TouchEvent) => {
        touchX = event.changedTouches[0].clientX;
        touchY = event.changedTouches[0].clientY;
    });

    window.addEventListener('touchend', (event:TouchEvent) => {
        if (timelyMove !== undefined) {
            clearInterval(timelyMove.timer);
        }
        timelyMove = undefined;
    });

    function setTimelyMove(dir: Direction) {
        if (timelyMove === undefined || timelyMove.dir != dir) {
            game.move(dir);
            if (timelyMove !== undefined) {
                clearInterval(timelyMove.timer);
            }
            timelyMove = {
                dir: dir,
                timer: setInterval(() => { game.move(dir); }, 500)
            };
        }
    }

    window.addEventListener('touchmove', (event:TouchEvent) => {
        const deltaX = touchX - event.changedTouches[0].clientX;
        const deltaY = touchY - event.changedTouches[0].clientY;
        if (Math.abs(deltaX) < 30 && Math.abs(deltaY) < 30) {
            return;
        }
        if (Math.abs(deltaX) > Math.abs(deltaY)) {
            if (deltaX > 0) {
                setTimelyMove('LEFT');
            } else {
                setTimelyMove('RIGHT');
            }
        } else {
            if (deltaY > 0) {
                setTimelyMove('UP');
            } else {
                setTimelyMove('DOWN');
            }
        }
    });
}